p {
  margin: 0;
  padding: 0;
}

.header {
  width: 100%;
  min-height: 60px;
  box-shadow: 2px 13px 19px -8px #bcbbbb;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  z-index: 1;
  position: fixed;
  top: 0;
  background-color: #fff;

  &__container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 0 50px;

    .logo_search {
      display: flex;
      align-items: center;
      gap: 50px;

      .logo {
        display: inline-block;
        width: 100px;
        height: 40px;
        transition: transform 0.3s;

        &:hover {
          transform: scale(1.1);
          transition: transform 0.3s;
        }
      }

      .search {
        padding: 5px 10px;
      }
    }

    .date__container {
      display: flex;
      flex-direction: column;
      gap: 5px;

      .day__name {
        font-size: 14px;
      }

      .timeContainer {
        display: flex;
        align-items: center;
        gap: 8px;

        .dayTime {
          font-size: 14px;
        }
      }
    }
  }
}